@import "reset";
@import "mixins";
@import "fonts";
@import "vars";
@import "generated/sprite-svg";
@import "libs/dropdownMenu";
@import "libs/slick";
@import "libs/slick-theme";
@import "libs/tabs";
@import "libs/magnific-popup";
@import "libs/custom-select";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

a {
  color: inherit;
  transition: all 0.2s ease;
}

textarea {
  resize: none;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col-full {
  flex-grow: 1;
}

.wrapper {
  width: 100%;
  font-family: $font-primary;
  color: #151515;
  background-color: #fdfdfd;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  @media only screen and (max-width: 1600px) {
    padding-left: 1%;
    padding-right: 1%;
  }
}

.paragraph {
  font-weight: 300;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn--more {
  width: 100%;
  max-width: 360px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6em 1.25em 0.5em;
  font-family: $font-futuraRound;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.7px;
  white-space: nowrap;
  color: #fdfdfd;
  border-color: #151515;
  background-color: #151515;
  transition: 0.3s;
  .icon-cheveron-right {
    font-size: 1.45em;
  }
  &:hover,
  &:focus {
    border-color: #ffffff;
    color: #008cc8;
    box-shadow: inset 360px 0 0 0 #ffffff;
    .icon-cheveron-right {
      color: #008cc8;
    }
  }
  .btn-text {
    padding-bottom: 5px;
  }
  .icon-cheveron-right {
    color: #ffffff;
    transition: 0.25s;
  }
  @media only screen and (max-width: 480px) {
    max-width: 280px;
    font-size: 18px;
  }
}

.bnt--right {
  margin: 0 80px 0 auto;
  border: 2px solid #fff;
  @media only screen and (max-width: 1200px) {
    margin-right: 0;
  }
}

.btn--green {
  border-color: #64884b;
  background-color: #64884b;
  &:hover,
  &:focus {
    border-color: #64884b;
    color: #64884b;
    .icon-cheveron-right {
      color: #64884b;
    }
  }
}

.btn--blue {
  border-color: #008cc8;
  background-color: #008cc8;
  &:hover,
  &:focus {
    border-color: #008cc8;
    color: #008cc8;
    .icon-cheveron-right {
      color: #008cc8;
    }
  }
}

.btn--red {
  border-color: #b92211;
  background-color: #b92211;
  &:hover,
  &:focus {
    border-color: #b92211;
    color: #b92211;
    .icon-cheveron-right {
      color: #b92211;
    }
  }
}

.btn--orange {
  border-color: #e28617;
  background-color: #e28617;
  &:hover,
  &:focus {
    border-color: #e28617;
    color: #e28617;
    .icon-cheveron-right {
      color: #e28617;
    }
  }
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: inherit;
  font-size: inherit;
  &:hover {
    cursor: pointer;
  }
}

.btn--load {
  display: block;
  margin: 0 auto;
  width: 180px;
  height: 40px;
  font-family: $font_futuraRound;
  font-size: 18px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.5px;
  color: #fdfdfd;
  background-color: #008cc8;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: lighten(#008cc8, 5%);
  }
  &.bg-red:hover {
    background-color: lighten(#b92211, 5%)
  }
}

.paragraph {
  font-size: 22px;
  line-height: 1.6;
  @media only screen and (max-width: 480px) {
    font-size: 16px;
  }
}

.text-bold {
  font-weight: 700;
}

.text-semibold {
  font-weight: 600;
}

.text-normal {
  font-weight: 400!important;
}

.underlined {
  text-decoration: underline;
}

sup {
  vertical-align: super;
  font-size: 0.6em;
}

.green {
  color: #64884b;
}

.blue {
  color: #008cc8;
}

.orange {
  color: #e28617;
}

.red {
  color: #b92211
}

.bg-black {
  background-color: #000000;
}

.bg-green {
  background-color: #64884b;
}

.bg-blue {
  background-color: #008cc8;
}

.bg-red {
  background-color: #b92211;
}

.bg-orange {
  background-color: #e28617;
}

.list {
  &__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
    @media only screen and (max-width: 480px) {
      margin-bottom: 20px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__disc {
    flex-grow: 0;
    flex-shrink: 0;
    width: 18px;
    height: 18px;
    margin-top: 9px;
    margin-right: 20px;
  }
  &__disc--green {
    background-color: #64884b;
  }
  &__disc--blue {
    background-color: #008cc8;
  }
  &__disc--orange {
    background-color: #e28617;
  }
  &__disc--red {
    background-color: #b92211;
  }
}

.shadowed {
  box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
}

.form {
  &-wrapper {
    padding: 30px 45px;
    @media only screen and (max-width: 768px) {
      padding: 30px 3vw;
      .form__inner {
        padding-left: 0;
      }
    }
  }
  .row--3 {
    @media only screen and (max-width: 992px) {
      flex-direction: column;
      align-items: center;
      input,
      .select_mate {
        width: 330px;
      }
      .form__field {
        flex-direction: column;
      }
    }
  }
  .row--2 {
    justify-content: space-around;
    @media only screen and (max-width: 580px) {
      flex-direction: column;
      align-items: center;
    }
    &>.col {
      &:first-child {
        .form__field {
          justify-content: space-between;
        }
      }
      &:last-child {
        .form__field {
          justify-content: flex-end;
        }
      }
      @media only screen and (max-width: 1550px) {
        .form__field {
          flex-direction: column;
        }
        input,
        .select_mate {
          width: 240px;
        }
      }
    }
    input,
    .select_mate {
      width: 170px;
    }
    .selecionado_opcion {
      padding: 5px 0px;
      margin: 0 27px 0 18px;
    }
    .cont_select_int li {
      padding: 11px 18px 11px 18px;
    }
  }
  &__caption {
    margin-bottom: 25px;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 2px;
    color: #a1a1a1;
    text-align: center;
    line-height: 1.3;
  }
  .divider {
    height: 1px;
    background-color: #a1a1a1;
  }
  &__inner {
    padding-top: 30px;
  }
  &__fields {
    padding: 25px 0;
  }
  &__field {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 45px;
  }
  label {
    margin-right: 30px;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.3;
    @media only screen and (max-width: 1360px) {
      br {
        display: none;
      }
    }
  }
  input {
    height: 29px;
    min-width: 147px;
    padding-left: 18px;
    border: none;
    max-width: 330px;
  }
  &__tip {
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.7px;
    text-align: center;
    font-weight: 600;
  }
  &__submit-btn {
    display: block;
    margin: 5px auto;
    width: 100%;
    max-width: 124px;
    height: 42px;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.3;
    text-align: center;
    color: #fdfdfd;
    transition: 0.3s;
    border: 1.5px solid transparent!important;
    background-color: #b92211;
    &:hover,
    &:focus {
      color: #b92211;
      box-shadow: inset 124px 0 0 0 #ffffff;
      border: 1.5px solid #a1a1a1;
    }
  }
}

.form--orange {
  label {
    color: #e28617;
  }
  .divider {
    background-color: #e28617;
  }
  .cont_select_int {
    li {
      &:hover {
        background-color: lighten(#e28617, 20%);
      }
    }
    .active {
      color: #fdfdfd;
      background-color: #e28617;
    }
  }
  .form__submit-btn {
    background-color: #e28617;
    &:hover,
    &:focus {
      color: #e28617;
      border-color: #e28617!important;
    }
  }
}

.form--red {
  label {
    color: #b92211;
  }
  .divider {
    background-color: #b92211;
  }
  .cont_select_int {
    li {
      &:hover {
        background-color: lighten(#b92211, 20%);
      }
    }
    .active {
      background-color: #b92211;
    }
  }
  .form__submit-btn {
    background-color: #b92211;
    &:hover,
    &:focus {
      color: #b92211;
      border-color: #b92211!important;
    }
  }
}

.main {
  padding-bottom: 210px;
  // the same as header height
  margin-top: 180px;
  @media only screen and (max-width: 1080px) {
    margin-top: 140px;
  }
}

// HEADER
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  background-color: #fdfdfd;
  .container {
    padding: 0 1px 0 30px;
    @media only screen and (max-width: 1080px) {
      padding: 0 3%;
      .header__inner {
        align-items: center;
        padding: 20px 0;
      }
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    padding: 50px 0 30px;
  }
  &__logo {
    width: 100px;
    min-width: 100px;
    margin-right: 3.75vw;
    img {
      display: block;
      width: 100%;
      max-width: 100px;
    }
  }
  &__nav {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
    @media only screen and (max-width: 1080px) {
      position: absolute;
      top: 100%;
      left: 0;
      z-index: 1;
      width: 100%;
      padding: 3%;
    }
  }
}

.nav {
  &>ul {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @media only screen and (max-width: 1080px) {
      flex-direction: column;
      width: 100%;
    }
  }
  &__item {
    margin-right: 3.5vw;
    @media only screen and (min-width: 1600px) {
      margin-right: 55px;
    }
    @media only screen and (max-width: 1080px) {
      margin-bottom: 8px;
      margin-right: 0!important;
    }
    @media only screen and (min-width: 1080px) {
      &.icon-cheveron-right:before {
        display: none;
      }
    }
    &:last-child {
      margin-right: 2.81vw;
    }
    ul li {
      padding-left: 10px;
      padding-right: 10px;
      transition: all 0.2s ease;
    }
    ul>li:last-child a {
      border: none!important;
    }
    &:nth-child(1) {
      &:hover {
        color: #64884b;
      }
      ul li {
        &:hover {
          background-color: #64884b;
        }
        a {
          border-bottom: 1px solid #64884b;
        }
      }
    }
    &:nth-child(2) {
      &:hover {
        color: #008cc8;
      }
      ul li {
        &:hover {
          background-color: #008cc8;
        }
        a {
          border-bottom: 1px solid #008cc8;
        }
      }
    }
    &:nth-child(3) {
      &:hover {
        color: #e28617;
      }
      ul li {
        &:hover {
          background-color: #e28617;
        }
        a {
          border-bottom: 1px solid #e28617;
        }
      }
    }
    &:nth-child(4) {
      &:hover {
        color: #b92211;
      }
      ul li {
        &:hover {
          background-color: #b92211;
        }
        a {
          border-bottom: 1px solid #b92211;
        }
      }
    }
    &:nth-child(5) {
      &:hover {
        color: #64884b;
      }
      ul li {
        &:hover {
          background-color: #64884b;
        }
        a {
          border-bottom: 1px solid #64884b;
        }
      }
    }
    &:nth-child(6) {
      &:hover {
        color: #008cc8;
      }
      ul li {
        &:hover {
          background-color: #008cc8;
        }
        a {
          border-bottom: 1px solid #008cc8;
        }
