    }
    a {
      font-family: $font-primary;
    }
    &>a {
      font-size: 18px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.5px;
      text-align: center;
      @media only screen and (max-width: 1080px) {
        text-align: left;
      }
      color: #151515;
      &:hover {
        color: inherit;
      }
    }
  }
}

.icon-instagram,
.icon-facebook2 {
