      font-size: 0.9em;
      font-weight: 300;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
    }
  }
  .tabbed {
    display: flex;
    @media only screen and (max-width: 600px) {
      display: block;
    }
  }
  .tabs__nav {
    li {
      display: block;
    }
    .tabs__label {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 450px;
      margin-bottom: 10px;
      padding: 0.7em 1em 0.7em 2.5em;
      font-family: $font-futuraRound;
      font-size: 24px;
      font-weight: bold;
      line-height: 1.3;
      color: #fdfdfd;
      background-color: #e28617;
      transition: all 0.2s ease;
      @media only screen and (max-width: 1200px) {
        width: 100%;
        padding-left: 1em;
        padding-right: 0.7em;
      }
      @media only screen and (max-width: 768px) {
        font-size: 16px;
      }
    }
    .tabs--green {
      background-color: #64884b;
    }
    a.tabs--green:hover {
      background-color: lighten(#64884b, 10%)
    }
    .tabs--blue {
      background-color: #008cc8;
    }
    a.tabs--blue:hover {
      background-color: lighten(#008cc8, 10%)
    }
    .tabs--orange {
      background-color: #e28617;
    }
    a.tabs--orange:hover {
      background-color: lighten(#e28617, 10%)
    }
    #tab6 {
      margin-bottom: 0;
    }
  }
  .tabs__name {
    white-space: nowrap;
  }
  .tabs__desc {
    font-family: $font-primary;
    font-size: 0.8em;
    font-weight: 300;
    white-space: nowrap;
  }
  [role=tablist] [aria-selected] {
    &.tabs--green {
      background-color: #a2b893;
    }
    &.tabs--blue {
      background-color: #5cceff;
    }
    &.tabs--orange {
      background-color: #f3cfa2;
    }
    .tabs__chevrons {
      opacity: 1;
    }
  }
  .tabs__chevrons {
    margin-left: 3vw;
    opacity: 0;
    white-space: nowrap;
    transition: opacity 0.2s ease;
    .icon-cheveron-right {
      font-size: 1.5em;
    }
    span:first-child {
      margin-right: -55px;
      @media only screen and (max-width: 768px) {
        margin-right: -20px;
      }
    }
  }
  .tabs__body {
    width: 100%;
    margin-left: 30px;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
    @media only screen and (max-width: 768px) {
      margin-left: 15px;
    }
    @media only screen and (max-width: 600px) {
      margin-left: 0;
      margin-top: 30px;
    }
  }
}

// table
.courses__table {
  padding: 10px 45px;
  @media only screen and (max-width: 992px) {
    padding: 10px 25px;
  }
  .row {
    padding: 15px 0 20px;
    border-bottom: solid 1.5px #64884b;
    &:last-child {
      border: none;
    }
    .col {
      flex-basis: 33%;
      &:first-child {
        flex-basis: 25%;
      }
    }
    &:not(.row__head) {
      .col:first-child {
        .paragraph {
          color: #64884b;
        }
      }
      .col:last-child {
        .paragraph {
          font-weight: 400;
        }
      }
    }
  }
  .paragraph {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.3;
    @media only screen and (max-width: 768px) {
      font-size: 14px;
    }
  }
  .row__head {
    .paragraph {
      text-transform: uppercase;
      color: #a1a1a1;
    }
  }
}

.courses__table--blue {
  .row {
    border-color: #008cc8;
    &:not(.row__head) {
      .col:first-child {
        .paragraph {
          color: #008cc8;
        }
      }
    }
  }
}

.courses__table--2 {
  color: #e28617;
  &>.row {
    border-bottom: solid 1.5px #e28617;
    &:last-child {
      border-bottom: solid 1.5px #e28617;
    }
  }
  .row {
    justify-content: center;
    &:not(.row__head) {
      .col:first-child {
        .paragraph {
          color: #e28617;
          font-weight: 400;
        }
      }
    }
    .paragraph:not(:last-child) {
      margin-bottom: 0.5em;
    }
  }
  .row__head {
    text-align: center;
    border-color: #f3cfa2;
  }
  &.one-col {
    text-align: center;
    color: #e28617;
  }
  &.two-col {
    .row {
      .col {
        flex-basis: 50%;
        @media only screen and (min-width: 992px) {
          min-width: 250px;
        }
      }
    }
  }
}

.courses__table--2green {
  .row {
    &:not(.row__head) {
      .paragraph {
        color: #64884b;
      }
      .col:first-child {
        .paragraph {
          color: #64884b;
        }
      }
    }
    &:last-child {
      border-color: #64884b;
    }
  }
  .row__head {
    border-color: #a2b893;
  }
}

.courses__table--2blue {
  .row {
    &:not(.row__head) {
      .paragraph {
        color: #008cc8
      }
      .col:first-child {
        .paragraph {
          color: #008cc8
        }
      }
    }
    &:last-child {
      border-color: #008cc8;
    }
  }
  .row__head {
    border-color: #5cceff
  }
}

// PAGE: CLASSES
.page-classes {
  .curriculum__tabs {
    margin-top: 100px;
  }
  .curriculum__tabs .tabbed {
    display: flex;
  }
  @media only screen and (max-width: 600px) {
    .curriculum__tabs [role=tabpanel] {
      margin-left: 10px;
      margin-top: 0;
    }
  }
}

.non-tabbed {
  display: flex;
  .tabs__body {
    padding-bottom: 10px;
    box-shadow: none;
    @media only screen and (max-width: 600px) {
      margin-top: 0!important;
      margin-left: 10px;
    }
  }
}

.schedule {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 89px;
    font-size: 20px;
    background-color: #fdfdfd;
    box-shadow: 0px 3px 11.3px 0.7px rgba(21, 21, 21, 0.09);
    &:not(:last-child) {
      margin-bottom: 10px;
    }
    @media only screen and (max-width: 768px) {
      height: 59px;
    }
  }
  &__link {
    font-weight: 600;
    line-height: 1.3;
    letter-spacing: 0.6px;
    text-align: center;
    text-transform: uppercase;
    @media only screen and (max-width: 768px) {
      font-size: 18px;
    }
    @media only screen and (max-width: 480px) {
      font-size: 14px;
    }
  }
}

// PAGE: STEM-PORTFOLIO
.page-stem-portfolio {
  .sticker__image {
    img {
      max-height: 418px;
      height: 100%;
      object-fit: cover
    }
  }
  .sticker__col .paragraph:not(:last-child) {
    margin-bottom: 2.45em;
  }
}

// PAGE: THEME PARTIES
.page-theme-parties {
  .events {
    margin-top: 0;
    margin-bottom: 4vw;
    &__title {
      margin-bottom: 1em;
    }
    &__item {
      padding: 2.5em 0;
      font-size: 18px;
    }
    &__pic {
      max-width: 212px;
    }
  }
  .venue {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 355px;
    margin-bottom: calc(60px + 4vw);
    font-size: 14px;
    letter-spacing: 0.8px;
    background-color: #f4f4f4;
  }
  .list {
    .mbt {
      margin-bottom: 1.5em;
    }
    &__item {
      margin-bottom: 10px;
      &>.row {
        @media only screen and (max-width: 580px) {
          flex-direction: column;
        }
        &>.col:first-child {
          flex-shrink: 0;
          display: flex;
          margin-bottom: 1.5em;
          @media only screen and (min-width: 580px) {
            width: 230px;
            margin-right: 20px;
          }
          .paragraph {
            font-size: 22px;
          }
        }
      }
      .row.mbt {
        flex-wrap: wrap;
      }
    }
    &__disc {
      margin-right: 14px;
    }
  }
  .content-wrap {
    padding-top: 40px;
    &>.row {
      @media only screen and (max-width: 1360px) {
        flex-wrap: wrap;
      }
      &>.col {
        &:first-child {
          margin-bottom: 90px;
          @media only screen and (min-width: 1360px) {
            min-width: 480px;
            max-width: 555px;
            margin-right: 85px;
          }
          &>.paragraph {
            margin-bottom: 2em;
          }
        }
        &:last-child {
          flex-shrink: 5;
        }
      }
    }
  }
}

// PAGE: EVENT
.page-event {
  .content-block {
    padding-top: 80px;
  }
  .event {
    &__title {
      margin-bottom: 1.3em;
      font-family: $font-futuraRound;
      font-size: 30px;
      font-weight: bold;
      letter-spacing: 0.9px;
      @media only screen and (max-width: 480px) {
        font-size: 22px;
      }
    }
    &__header {
      max-width: 1100px;
      .paragraph {
        margin-bottom: 2em;
      }
    }
  }
}

// PAGE: CONTACT US
.page-contact {
  .content-wrap {
    padding-top: 90px;
    &>.row {
      @media only screen and (max-width: 768px) {
        display: block;
      }
      &>.col {
        &:first-child {
          margin-right: 45px;
          flex-shrink: 0;
          max-width: 480px;
          @media only screen and (min-width: 1200px) {
            min-width: 480px;
          }
        }
      }
    }
  }
  .content-block {
    margin-bottom: 4em;
    .paragraph {
      margin-bottom: 1em;
    }
    &__title.paragraph {
      margin-bottom: 3em;
      @media only screen and (max-width: 768px) {
        margin-bottom: 1.5em;
      }
    }
  }
  .social-icons {
    display: flex;
    a {
      margin-right: 23px;
    }
  }
  .form__attraction {
    margin-bottom: 60px;
    .paragraph {
      margin-bottom: 1em;
    }
  }
  .form__field {
    display: block;
    margin-bottom: 1em;
    label {
      color: #151515;
    }
    input,
    textarea {
      display: block;
      width: 100%;
      margin-top: 0.7em;
      padding: 1.4em 1em;
      font-size: 22px;
      font-family: inherit;
      border: none;
      @media only screen and (max-width: 992px) {
        font-size: 16px;
        padding: 10px 1em;
      }
    }
  }
  .form__submit-btn {
    margin-top: 1.5em;
    margin-left: 0;
    font-size: 24px;
    font-family: $font-futuraRound;
  }
}

// PAGE: NEWS & MEDIA
.posts-wrap {
  padding-top: 90px;
  max-width: 1280px;
  &>.row {
    flex-wrap: wrap;
    @media only screen and (max-width: 992px) {
      justify-content: space-around;
    }
  }
}

.post {
  flex-basis: 31%;
  margin-top: 30px;
  margin-bottom: 85px;
  @media only screen and (max-width: 992px) {
    flex-basis: 45%;
    margin-bottom: 50px;
  }
  @media only screen and (max-width: 580px) {
    flex-basis: 90%;
  }
  &__img {
    img {
      display: block;
      width: 100%;
      height: 100%;
      max-height: 394px;
      object-fit: cover;
      object-position: center;
    }
  }
  &__title {
    margin: 0.8em 0 1em;
    font-size: 22px;
    line-height: 1.36;
    font-weight: bold;
    @media only screen and (max-width: 768px) {
      font-size: 18px;
      br {
        display: none;
      }
    }
  }
  &__date {
    margin-bottom: 1em;
    font-size: 18px;
    font-weight: normal;
    @media only screen and (max-width: 768px) {
      font-size: 15px;
    }
  }
  &__text {
    font-size: 18px;
    @media only screen and (max-width: 768px) {
      font-size: 15px;
    }
  }
}