  .menu-mobile {
    display: none;
    padding: 20px;
    &:after {
      content: "\f394";
      font-family: "Ionicons";
      font-size: 2.5rem;
      padding: 0;
      float: right;
      position: relative;
      top: 50%;
      transform: translateY(-25%);
    }
  }
  
  .menu-dropdown-icon {
    &:before {
      content: "\f489";
      font-family: "Ionicons";
      display: none;
      cursor: pointer;
      float: right;
      padding: 1.5em 2em;
      background: #fff;
      color: #151515;
    }
  }
  
  .menu {
    .icon-cheveron-right:before {
      position: absolute;
      right: 0;
      top: 5px;
      font-size: 22px;
    }
    >ul {
      display: flex;
      list-style: none;
      padding: 0;
      height: 100%;
      position: relative;
      //position: relative;
      /* IF .menu position=relative -> ul = container width, ELSE ul = 100% width */
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      &:before,
      &:after {
        content: "";
        display: table;
      }
      &:after {
        clear: both;
      }
      >li {
        position: relative;
        a {
          height: 100%;
          text-decoration: none;
          display: flex;
          align-items: center;
          @media only screen and (min-width: 1081px) {
            sup {
              height: 20px;
              display: flex;
              align-items: flex-start;
            }
          }
        }
        >ul {
          display: none;
          width: 100%;
          background: #fdfdfd;
          padding: 20px;
          position: absolute;
          z-index: 99;
          left: 0;
          margin: 0;
          list-style: none;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;
          &:before,
          &:after {
            content: "";
            display: table;
          }
          &:after {
            clear: both;
          }
          >li {
            margin: 0;
            padding-bottom: 0;
            list-style: none;
            a {
              color: inherit;
              padding: .2em 0;
              display: block;
            }
            >ul {
              display: block;
              padding: 0;
              margin: 10px 0 0;
              list-style: none;
              -webkit-box-sizing: border-box;
              -moz-box-sizing: border-box;
              box-sizing: border-box;
              &:before,
              &:after {
                content: "";
                display: table;
              }
              &:after {
                clear: both;
              }
              >li {
                float: left;
                width: 100%;
                padding: 10px 0;
                margin: 0;
                font-size: .8em;
                a {
                  border: 0;
                }
              }
            }
          }
          &.normal-sub {
            width: 180px;
            left: auto;
            padding: 0;
            box-shadow: 0px 3px 8.5px 0.5px rgba(250, 169, 169, 0.09);
            >li {
              width: 100%;
              a {
                padding: 0.7em 0;
                font-size: 16px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.25;
                letter-spacing: 0.7px;
                text-align: center;
                @media only screen and (max-width: 1080px) {
                  text-align: left;
                }
                color: #151515;
                &:hover {
                  color: #ffffff;
                }
              }
            }
          }
        }
      }
    }
  }
  /* ––––––––––––––––––––––––––––––––––––––––––––––––––
Mobile style's
–––––––––––––––––––––––––––––––––––––––––––––––––– */
  
  @media only screen and (max-width: 1080px) {
    .menu-container {
      width: 100%;
    }
    .menu-mobile {
      display: none;
    }
    .menu-dropdown-icon {
      &:before {
        display: block;
      }
    }
    .menu {
      >ul {
        display: none;
        >li {
          width: 100%;
          float: none;
          display: block;
          a {
            padding: 0.2em 0;
            width: 100%;
            display: block;
          }
          >ul {
            position: relative;
            &.normal-sub {
              width: 100%;
            }
            >li {
              float: right;
              width: 100%;
              &:first-child {
                margin: 0;
              }
              >ul {
                position: relative;
                >li {
                  float: none;
                }
              }
            }
          }
        }
      }
      .show-on-mobile {
        display: block;
      }
    }
  }