 .icon_select_mate {
   position: absolute;
   top: 1px;
   bottom: 0;
   right: 2%;
   margin: auto;
   font-size: 18px;
   transition: all 275ms;
 }
 
 .select_mate {
   position: relative;
   height: 29px;
   width: max-content;
   max-width: 330px;
   transition: all 375ms ease-in-out;
   /* Oculto el elemento select */
   select {
     position: absolute;
     overflow: hidden;
     height: 0px;
     opacity: 0;
     z-index: -1;
   }
 }
 
 .cont_list_select_mate {
   position: relative;
   width: 100%;
 }
 
 .cont_select_int {
   position: absolute;
   left: 0px;
   top: 0px;
   z-index: 999;
   overflow: hidden;
   height: 0px;
   width: 100%;
   margin-bottom: 0px;
   margin-top: 8px;
   background-color: #fdfdfd;
   transition: all 375ms ease-in-out;
   li {
     position: relative;
     display: block;
     width: 100%;
     list-style-type: none;
     padding: 11px 18px 11px 36px;
     margin: 0px;
     font-weight: 600;
     line-height: 1.25;
     letter-spacing: 0.7px;
     transition: all 275ms ease-in-out;
     cursor: pointer;
     &:hover {
       color: #fdfdfd;
       background-color: #EEEEEE;
     }
   }
   li:not(:last-child) {
     &:after {
       content: "";
       position: absolute;
       bottom: 0;
       left: 0;
       right: 0;
       margin: auto;
       display: block;
       height: 1px;
       width: calc(100% - 36px);
       background-color: #b92211;
     }
   }
   .active {
     color: #fdfdfd;
     background-color: #EEEEEE;
   }
 }
 /* etiqueta <p> con la opcion selecionada  */
 
 .selecionado_opcion {
   padding: 5px 36px;
   white-space: nowrap;
   display: block;
   margin: 0px;
   cursor: pointer;
   font-size: 16px;
   font-weight: 600;
   line-height: 1.25;
   letter-spacing: 0.7px;
   text-align: left;
   color: #151515;
   overflow: hidden;
 }