[role="tablist"] {
  padding: 0;
}

[role="tablist"] li,
[role="tablist"] a {
  display: inline-block;
}

[role="tablist"] [aria-selected] {
  position: relative;
}

*:focus {
  outline: none;
}

.gallery__grid {
  [role="tablist"] {
    margin-bottom: 25px;
    font-family: $font-futuraRound;
    font-size: 30px;
    font-weight: bold;
    line-height: normal;
    letter-spacing: 0.9px;
    color: #a1a1a1;
    @media only screen and (min-width: 1200px) {
      padding-left: 320px;
    }
  }
  [role="tablist"] li,
  [role="tablist"] a {
    display: inline-block;
  }
  [role="tablist"] a {
    text-decoration: none;
    @media only screen and (max-width: 768px) {
      font-size: 24px;
    }
    &:hover {
      color: #e28617;
    }
  }
  [role="tablist"] [aria-selected] {
    position: relative;
    color: #e28617;
  }
  [role="tabpanel"] {
    padding: 10px 0 90px;
    @media only screen and (max-width: 1200px) {
      padding-bottom: 60px;
    }
  }
}