.page-contact .form__field label { color: #151515; }

.page-contact .form__field input, .page-contact .form__field textarea { display: block; width: 100%; margin-top: 0.7em; padding: 1.4em 1em; font-size: 22px; font-family: inherit; border: none; }

@media only screen and (max-width: 992px) { .page-contact .form__field input, .page-contact .form__field textarea { font-size: 16px; padding: 10px 1em; } }

.page-contact .form__submit-btn { margin-top: 1.5em; margin-left: 0; font-size: 24px; font-family: "FuturaRound", sans-serif; }

.posts-wrap { padding-top: 90px; max-width: 1280px; }

.posts-wrap > .row { flex-wrap: wrap; }

@media only screen and (max-width: 992px) { .posts-wrap > .row { justify-content: space-around; } }

.post { flex-basis: 31%; margin-top: 30px; margin-bottom: 85px; }

@media only screen and (max-width: 992px) { .post { flex-basis: 45%; margin-bottom: 50px; } }

@media only screen and (max-width: 580px) { .post { flex-basis: 90%; } }

.post__img img { display: block; width: 100%; height: 100%; max-height: 394px; object-fit: cover; object-position: center; }

.post__title { margin: 0.8em 0 1em; font-size: 22px; line-height: 1.36; font-weight: bold; }

@media only screen and (max-width: 768px) { .post__title { font-size: 18px; }
  .post__title br { display: none; } }

.post__date { margin-bottom: 1em; font-size: 18px; font-weight: normal; }

@media only screen and (max-width: 768px) { .post__date { font-size: 15px; } }

.post__text { font-size: 18px; }

@media only screen and (max-width: 768px) { .post__text { font-size: 15px; } }
